@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Otomanopee+One&family=Qahiri&display=swap);
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
html{
  scroll-behavior: smooth;
}
body{
  font-family: 'Roboto', sans-serif;
  overflow-x: hidden;
}
/* css variables */
:root {
  --dark-blue: #0a2538;
  --light-blue:#0277de;
  --white:#fff;
  --snow-white:#f5f5f5;
  --black:#000;
  --light-black:rgb(95, 94, 94);
}
.btn-link{
  color:#fff !important;
  color:var(--white) !important;
  text-decoration: none !important;
}
a:hover{
  color:#fff;
  color:var(--white);
  /* text-decoration: none; */
}
p{
  font-size: 15px;
  color: rgb(95, 94, 94);
  color: var(--light-black);
  margin-bottom: 10px !important;
}


::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #fff;
  background: var(--white);
  box-shadow: inset 0 0 5px grey;
  background-clip: content-box;

}
::-webkit-scrollbar-thumb {
  background-color: #0277de;
  background-color: var(--light-blue);
  border-radius: 50px;
}

/* decoration of some part of header and about section */
.main_container{
    width: 100vw;
    height: 100vh !important;
    background-color:  var(--dark-blue);
}
.sub_container{
    width: 100% !important;
    height: 100vh !important;
}
.content_left_container,.content_right_container{
    height: 100vh !important;
    padding: 0;
    display: flex;
    align-items: center;
}
.content_left_container{
    padding: 0 2px 0 0;
}
/* ends here */
.content_left,.content_right{
    width: 100% !important;
    height: 50vh !important;
    display: flex;
    justify-content: center;
    flex-direction: column;
    /* background-color: #000; */
    color: var(--white) ;
    letter-spacing: 1.5px !important;
}
.content_left h4{
    margin: 0;
}
.content_left h2{
    text-shadow: 0 2px 0 rgba(0,0,0,.5);
}
.content_left p{
    color: inherit;
}
.content_left h2 span::before{

    content:'';
    /* padding-left:3px; */
    -webkit-animation: animate infinite  4s ease-in-out;
            animation: animate infinite  4s ease-in-out;
}
@-webkit-keyframes animate{
    0%{
        content: "Bushra Nikhat";
    }
    100%{
        content: "Web developer";
    }
}
@keyframes animate{
    0%{
        content: "Bushra Nikhat";
    }
    100%{
        content: "Web developer";
    }
}
.connect{
    margin-bottom: 16px;
}
.connect span{
    margin-right: 10px;
    cursor: pointer;
}
.connect span .iconButton{
color: white;
padding: 0;
}

.content_right{
    align-items:flex-end;
    
}
.header_image{
    height: 90%;
    width: 90%;
 
}

/* large */
@media (min-width: 992px) and (max-width: 1199px) {
    .header_image{
        width: 100%;
        height: 80%;
     
    }
   
}
/* medium */
@media (min-width:768px) and (max-width:991px) {
    .header_image{
        height: 70%;
        width: 100%;
     
    }
   
}
/* small */
@media (min-width:576px) and (max-width:767px) {
    .header_image{
        display: none;
    }
    .content_left{
        letter-spacing:.9px !important;
    }  
}
/*x- small */
@media (max-width:575px){
    .header_image{
        display: none;
     
    }
   
   
}

/* landscape mode */

@media (max-height:500px) and (orientation:landscape){
    .header_right_container{
        display: none;
    }
    .header_image{
        display: none;
     
    }
    .content_left p{
        display: block;
    }
}
.nav{
    position: fixed;
    top: 0;
    width: 100vw;
    display: flex;
}
.nav-transparent{
    background-color:transparent;
    color: var(--white);
}
.nav-white{
    background-color: var(--white);

}

.navbar-toggler{
    outline: none !important;
}
.navbar-toggler:focus{
    box-shadow: none;
}
.navbar-brand{
    color: var(--white);
    font-size: 24px;
    font-family: 'Otomanopee One', sans-serif;
    letter-spacing: 2px;
}

.nav-link{
    font-size: 16px;
}

.nav-link:hover{
    color: var(--light-blue) !important;
}
.nav-link.active {
    color:  var(--light-blue) !important;
  }

.navbar-nav .nav-item:last-child .nav-link{
    padding-right: 0 !important;
}

/* landscape mode */

@media (max-height:500px) and (orientation:landscape){
    .nav-transparent{
        background-color: var(--dark-blue);
        color: var(--white);
    }

}

.about{
    background-color: var(--white) !important;
}
.about_container{
