.main{
    width: 100%;
    height: 300px;
    /* background-color: #000; */
   position: relative;
}
.item{
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
}
.center_image{
    width: 120px;
    height: 120px;
    border-radius: 50%;
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-32%,-32%);
            transform: translate(-32%,-32%);
}

.item{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    position: absolute;
    -webkit-animation: spinAround 7s linear infinite;
            animation: spinAround 7s linear infinite;

}
@-webkit-keyframes spinAround{
    from{
        -webkit-transform: rotate(0deg)  translate(120px) scale(1);
                transform: rotate(0deg)  translate(120px) scale(1);
    }
    to{-webkit-transform: rotate(360deg) translate(120px) scale(1);transform: rotate(360deg) translate(120px) scale(1);}
}
@keyframes spinAround{
    from{
        -webkit-transform: rotate(0deg)  translate(120px) scale(1);
                transform: rotate(0deg)  translate(120px) scale(1);
    }
    to{-webkit-transform: rotate(360deg) translate(120px) scale(1);transform: rotate(360deg) translate(120px) scale(1);}
}


