.portfolio{
    background-color: var(--snow-white);
}
.container{
    width: 100%;
    height: 100vh;
}
.title{
    height: 16%;
    display: flex;
    justify-content: center;
    align-items: flex-end;
}

.image_row{
        height: 84%;  
        width: 100%;
}
.image_container{
    height: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    /* border: 1px solid red; */
}
.image_sub_container{
    width: 85%;
    height: 85%;
    position: relative;
    
}
.image{
    width: 100%;
    height: 100%;
}
.overlay{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    transition: .5s ease-in;
    width:0%;
    height: 0%;
    visibility:hidden;
    background-color: rgba(0,0,0,.6);
    color:var(--white);

}
.overlay_content h4{
    width: 100%;
    text-align: center;
}





.overlay_content h4,.overlay_content .project_btn{
    position: absolute;
    top: 35%;
    left: 50%;
    -webkit-transform: translate(-50%);
            transform: translate(-50%);
    opacity: 0;
    color: inherit;
    transition: opacity .01s ease-in  .3s;
  }
  .overlay_content .project_btn{
      top: 45%;
      color: var(--white);
  }
  .image_sub_container:hover > .overlay{
      width: 100%;
      height: 100%;
      visibility:visible;
  }
  .image_sub_container:hover > .overlay h4,.image_sub_container:hover > .overlay .project_btn{
      opacity: 1;
  }


/* large */
@media (min-width: 992px) and (max-width: 1199px) {
    .image_sub_container{
        width: 90%;
        min-width: 255px;
    }
   
}

/* medium */
@media (min-width:768px) and (max-width:991px) {
    .image_container{
        height: 33.33%;
      
    }
    .image_sub_container{
        width: 88%;
        height: 88%;
        
    }
   
}

/* small */
@media (min-width:576px) and (max-width:767px) {
    .image_container{
      width: 100%;
    }
    .image_sub_container{
        width: 70%;
        height: 70%;
        
    }
    .image_row{
        overflow-y: scroll;
    }
   
}
@media (max-width:575px){
    
.image_row{
    overflow-y: scroll;
}
   
}
/* landscape mode */

@media (max-height:500px) and (orientation:landscape){
    .image_row{
        overflow-y: scroll;
    }
    .image_container{
        height: 90%;
    
    }
}
